import React,{useContext} from 'react';
import CartItem from './CartItem';
import Cartcontext from '../../store/Cart-context';
import Modal from '../UI/modal';
import styles from './Cart.module.css';
 
const Cart = (props) => {
    
const addCarthandler = () => {}
const removeCartHandler = () => {}
    const cartctx = useContext(Cartcontext);
    const totalAmount = `$${cartctx.totalAmount.toFixed(2)}`
    const hascart = cartctx.items.length > 0;
        const cartItem =
    (<ul className={styles['cart-items']}> {cartctx.items.map((item)=><CartItem name={item.name} 
                                                                                price={item.price}
                                                                                amount={item.amount}
                                                                                key={item.id}
                                                                                onadd = {addCarthandler.bind(null,item.id)}
                                                                                onremove = {removeCartHandler.bind(null,item.id)}
                                                                                />)}
    </ul>
    )

    return(<Modal onCloseCart={props.onCloseCart}>
        <li key={props.id}>{cartItem}</li>
        <div className={styles.total}>
            <div>TotalAmount</div>
            <div>{totalAmount}</div>
        </div>
        <div className={styles.actions}>
            <button className={styles['button--alt']} onClick={props.onCloseCart}>Close</button>
            {hascart &&<button className={styles.button}>Order</button>}

        </div>
    </Modal>)
}

export default Cart;